import React,{ useEffect,useState } from 'react';
import logo from './images/header_mob.png';
import './App.css';
import ScrollMemory from 'react-router-scroll-memory';
import { HashRouter as Router, Route, Link } from "react-router-dom";

// Firebase App (the core Firebase SDK) is always required and
// must be listed before other Firebase SDKs
import * as firebase from "firebase/app";

// Add the Firebase services that you want to use
import "firebase/database";

var firebaseConfig = {
  apiKey: "AIzaSyDbPyJdwBCo6cPwNh0j3u2RYFkuFJXHjls",
  authDomain: "sketchyrhymes.firebaseapp.com",
  databaseURL: "https://sketchyrhymes.firebaseio.com",
  projectId: "sketchyrhymes",
  storageBucket: "sketchyrhymes.appspot.com",
  messagingSenderId: "127981771828",
  appId: "1:127981771828:web:34e76f0d5d956b05"
};

// Initialize Firebase
firebase.initializeApp(firebaseConfig);

var database = firebase.database();





function Home(props){
  let headImg = React.createRef();
  let header = React.createRef();
  let blogger = React.createRef();
  let thenavigation = React.createRef();
  let sketchNav = React.createRef();
  let rhymeNav = React.createRef();


  const [showSketches,setShowSketches]=useState(1);
  const [showRhymes,setShowRhymes]=useState(1);
  const [posts,setPosts]=useState(null);

  useEffect(() => {
    console.log(props.location.state)
    var postsRef = database.ref('/');
    postsRef.once('value',(snapshot)=>setPosts(snapshot.val()));
  },[]);
  const toggleSketches=()=>{
    if(showSketches==1){
      setShowSketches(0);
      sketchNav.current.classList.remove('selectedOption');
    }
    else{
      setShowSketches(1);
      sketchNav.current.classList.add('selectedOption');
    }
    removeListener();
  }
  const toggleRhymes=()=>{
    if(showRhymes==1){
      setShowRhymes(0);
      rhymeNav.current.classList.remove('selectedOption');
    }
    else{
      setShowRhymes(1);
      rhymeNav.current.classList.add('selectedOption');
    }
    removeListener();
  }

  const handleScroll=()=>{
    try{
      if(window.pageYOffset>30){
        headImg.current.classList.add('hideHeader');
        thenavigation.current.classList.add('navbarshow');
        header.current.classList.add('App-with-blueBG');
        blogger.current.classList.add('blogUp');
      }
      else{
        headImg.current.classList.remove('hideHeader');
        thenavigation.current.classList.remove('navbarshow');
        header.current.classList.remove('App-with-blueBG');
        blogger.current.classList.remove('blogUp');
      }
    }
    catch(e){

    }
  }
  window.addEventListener('scroll', handleScroll);

  const removeListener= ()=>window.removeEventListener('scroll',handleScroll);

  return (
    <div className="App" onScroll={handleScroll}>
      <div ref={thenavigation} className="navbar">
        <b className="sketchy selectedOption" ref={sketchNav} onClick={toggleSketches}>SKETCHY</b><b onClick={toggleRhymes} ref={rhymeNav} className="rhymes selectedOption">RHYM.ES</b>
      </div>
      <div>
        <header ref={header} className="App-header">
          <img ref={headImg} src={logo} className="App-logo" alt="logo" />
          <span className="scrollDown">SCROLL DOWN</span>
        </header>
      </div>
      <div ref={blogger} className="blog">
      { 
        posts==null
        ? null
        : <div>
            {Object.keys(posts).map(item => (posts[item].rhyme==1&&showRhymes==1 || posts[item].sketch==1&&showSketches==1)?<FeedPost location={props.location} rem={removeListener} item={item} post={posts[item]}/>:"")}
          </div>
      }    
      </div>
      
      
      <footer>
        Crafted with <b>love</b> by Shikhar
      </footer>
    </div>
  );
}
function App() {
  const [post, setPost] = useState(-1);
  return (
    <div className="App">
      <Router>
        <div>
          <ScrollMemory />
          <Route path="/" exact component={Home} />
          <Route path="/post/:id" component={BlogPost} />
        </div>
      </Router>
    </div>
  );
}

function FeedPost(props){
  let postRef = React.createRef();

  const data=props.post;
  useEffect(()=>{
    if(props.location.state==props.item)
      window.scrollTo(0, postRef.current.offsetTop-400);
  });
  return(
    <div ref={postRef} id={props.item} className="blogPost">
      <span className="postType">{data.sketch==1?"SKETCH":"RHYME"}</span>
      <b>{data.title}</b>
      <Link onClick={props.rem} to={`post/${props.item}`}><img src={data.cover}/></Link>
      <p>{data.caption}</p>
    </div>
  );
}

function BlogPost(props){
  const [data,setData]=useState(null);

  

  useEffect(() => {
    var dataRef = database.ref(`/${props.match.params.id}`);
    dataRef.once('value', (snapshot)=> setData(snapshot.val()));
  },[]);
  if(data==null){
    return null;
  }
  return( 
    <div className="blogPage">
      <div className="postTopSection">
      <Link to={{'pathname':'/','state':props.match.params.id}}><button className="backButton mob">Go Back</button></Link>
        <b className="titleText mob">{data.title}</b>
        <div className="postImages">
          <img src={data.cover}/>
          {data.images==undefined?null:data.images.map(item=><img src={item}/>)}
        </div>
      </div>
      <section className="textContent">
      <Link to={{'pathname':'/','state':props.match.params.id}}><button className="backButton nonmob">Go Back</button></Link>
        <b className="titleText nonmob">{data.title}</b>
        <p className="caption">{data.caption}</p>
        <p>
          {data.text.split("\n").map(item=><span>{item}<br/></span>)}  
        </p>
      </section>
    </div>
  
  );
}
export default App;
